<template>
<div
  class="veui-grid-row"
  :style="style"
>
  <slot/>
</div>
</template>

<script>
import 'veui/locale/zh-Hans/common.js'
import 'veui/locale/en-US/common.js'

export default {
  name: 'veui-grid-row',
  provide () {
    let { columns, gutter, flex } = this

    return {
      columns,
      gutter,
      flex
    }
  },
  inject: ['columns', 'gutter', 'flex'],
  computed: {
    style () {
      let { flex, gutter } = this
      return {
        ...flex
          ? {
            display: ['-webkit-box', '-ms-flexbox', 'flex']
          }
          : {},
        'margin-right': `-${gutter / 2}px`,
        'margin-left': `-${gutter / 2}px`
      }
    }
  }
}
</script>
<style lang="less" src="veui-theme-blue/components/grid-row.less"></style>



