<template>
<transition name="veui-toast">
  <div
    v-if="open"
    :ui="realUi"
    class="veui-toast"
    :class="`veui-toast-${type}`"
    role="alert"
  >
    <veui-icon
      class="veui-toast-icon"
      :name="icons[type]"
    />
    <span class="veui-toast-message">
      <slot>{{ message }}</slot>
    </span>
  </div>
</transition>
</template>

<script>
import 'veui/locale/zh-Hans/common.js'
import 'veui/locale/en-US/common.js'
import 'veui-theme-blue/components/Toast.js'

import Icon from './Icon'
import ui from '../mixins/ui'
import config from '../managers/config'
import { includes } from 'lodash'

config.defaults({
  duration: 3000
}, 'toast')

const TYPE_LIST = ['success', 'warning', 'info', 'error']

export default {
  name: 'toast',
  components: {
    'veui-icon': Icon
  },
  mixins: [ui],
  props: {
    type: {
      type: String,
      default: 'success',
      validator (val) {
        return includes(TYPE_LIST, val)
      }
    },
    message: String,
    open: Boolean,
    duration: {
      type: Number,
      default: config.get('toast.duration')
    }
  },
  mounted () {
    this.timer = setTimeout(() => {
      this.$emit('update:open', false)
      this.$emit('close')
    }, this.duration)

    this.$emit('ready', this.$el.offsetHeight)
  },
  beforeDestroy () {
    clearTimeout(this.timer)
  }
}
</script>
<style lang="less" src="veui-theme-blue/components/toast.less"></style>



